}

.btn::after, .btn::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 28px;
  content: '';
}

.btn span {
  position: relative;
  z-index: 1;
}

.btn_red {
  color: #fff;
}

.btn_red:hover::after {
  transform: translateY(0);
  box-shadow: none;
  content: '';
}

.btn_red::after {
  transform: translateY(5px);
  background-color: #9c1019;
  box-shadow: 0 0 48px 3px rgba(240, 67, 79, 0.2);
  transition: .3s ease-in-out;
}

.btn_red::before {
  z-index: 1;
