//fonts
@mixin font($alias, $name) {
   @font-face {
      font-family: $alias;
      src: url("../fonts/" + $name + ".eot");
      src: url("../fonts/" + $name + ".eot?#iefix") format("embedded-opentype"), url("../fonts/" + $name + ".woff") format("woff"), url("../fonts/" + $name + ".woff2") format("woff2"), url("../fonts/" + $name + ".ttf") format("truetype");
      font-weight: normal;
      font-style: normal; } }

@include font('MyriadPro-Regular', 'MyriadPro-Regular');
@include font('Ubuntu-Bold', 'Ubuntu-Bold');
@include font('Ubuntu-Light', 'Ubuntu-Light');
@include font('Ubuntu-Medium', 'Ubuntu-Medium');
@include font('Ubuntu-Regular', 'Ubuntu-Regular');

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
   $font-family: null;
   @if $name == "mp400" {
      $font-family: "MyriadPro-Regular"; }
   @if $name == "ub700" {
      $font-family: "Ubuntu-Bold"; }
   @if $name == "ub300" {
      $font-family: "Ubuntu-Light"; }
   @if $name == "ub500" {
      $font-family: "Ubuntu-Medium"; }
   @if $name == "ub400" {
      $font-family: "Ubuntu-Regular"; }
   font-family: $font-family, Arial, sans-serif;
   @if $font-family != null {
      font-weight: normal; }
   @if $font-size != null {
      font-size: $font-size * 1px; }
   @if $line-height != null {
      line-height: $line-height * 1px; }
   @if $letter-spacing != null {
      letter-spacing: ($letter-spacing / 1000) * 1em; } }
