body {
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   @include f('ub400', 16, 24);
   color: $color3;
   &.is-locked {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: 0;
      overflow: hidden; } }

.container {
   position: relative;
   z-index: 1;
   max-width: 1170px;
   margin: 0 auto;
   padding: 0 15px; }

.row {
   display: flex;
   margin: 0 -15px; }

.main {
   overflow: hidden;
   padding-top: 130px; }

.section {
   position: relative; }

.section-title {
   $parent: &;
   position: relative;
   &::before {
      position: absolute;
      width: 3px;
      content: ''; }
   &_left {
      padding-left: 30px;
      text-align: left;
      &::before {
         top: 0;
         left: 0;
         height: 100%;
         background-color: $color1;
         content: ''; } }
   &_center {
      text-align: center;
      &::after {
         left: calc(50% - 2px);
         bottom: 0;
         height: 25px;
         content: ''; } }
   &__inner {
      display: flex;
      #{$parent}_center & {
         justify-content: center; }
      #{$parent}_left & {
         justify-content: flex-start; } }
   &__icon {
      margin: -5px -20px 0 0;
      flex-shrink: 0; }
   &__text {
      @include f('ub700', 40, 60);
      text-transform: uppercase;
      #{$parent}_black & {
         color: $color2; }
      #{$parent}_white & {
         color: $white; }
      span {
         @include f('ub300'); } }
   &__descr {
      margin-top: 10px;
      color: $color3;
      @include f('ub300', 22, 34);
      &_color-1 {
         color: #e6e6e6; }
      &_color-2 {
         color: #e5e5e5; } } }

.title {
   @include f('ub700', 16, 24);
   text-transform: uppercase;
   &_black {
      color: $color2; }
   &_white {
      color: $white; } }

strong {
   @include f('ub500'); }

.btn {
   position: relative;
   display: inline-block;
   padding: 16px 42px;
   outline: 0;
   @include f('ub500', 16, 24);
   text-align: center;
   border-radius: 28px;
   cursor: pointer;
   &::after, &::before {
      @include coverdiv;
      border-radius: 28px;
      content: ''; }
   span {
      position: relative;
      z-index: 1; }
   &_red {
      color: $white;
      &:hover {
         &::after {
            transform: translateY(0);
            box-shadow: none;
            content: ''; } }
      &::after {
         transform: translateY(5px);
         background-color: $color7;
         box-shadow: 0 0 48px 3px rgba(240, 67, 79, 0.2);
         transition: .3s ease-in-out; }
      &::before {
         z-index: 1;
         background-color: $color1; } } }

.arc-decor {
   $parent: &;
   position: absolute;
   width: 100%;
   z-index: 2;
   bottom: 0;
   height: 150px;
   overflow: hidden;
   &__mask {
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: auto;
      &_top {
         #{$parent}_color-1 & {
            fill: $color8; }
         #{$parent}_color-2 & {
            fill: $color8;
            opacity: .1; } }
      &_bot {
         #{$parent}_color-1 &,
         #{$parent}_color-2 & {
             fill: $white; } } } }

.noise-bg {
   $parent: &;
   position: relative;
   background: #17171a url(../img/decor/noise_black.png);
   &::before, &::after {
      position: absolute;
      z-index: 1;
      @include cover;
      content: ''; }
   &_type-1 {
      &::before {
         top: 30%;
         left: 0;
         width: 808px;
         height: 803px;
         background: url("../img/decor/tire-tracks_l.png"); }
      &::after {
         top: 20%;
         right: 0;
         width: 491px;
         height: 633px;
         background: url("../img/decor/tire-tracks_r.png"); } } }
