// *** begin price-block *** //
.price-block {
   @include f('mp400', 36, 36);
   &_white {
      color: $white; }
   span {
      @include f('ub700'); } }
// end price-block //

// *** begin list-triangled *** //
.list-triangled {
   li {
      font-size: 0;
      &:not(:last-child) {
         margin-bottom: 6px; }
      &::before {
         display: inline-block;
         vertical-align: middle;
         margin-right: 7px;
         @include arr(4, 4, $color1, r);
         content: ''; }
      span, strong {
         display: inline;
         vertical-align: middle;
         font-size: 16px;
         line-height: 22px; }
      strong {
         margin-right: 4px; } } }

// end list-triangled //

// *** begin .scroll-next-btn *** //
.scroll-next-btn {
   position: absolute;
   left: calc(50% - 50px);
   width: 108px;
   z-index: 2;
   @include flexCenter;
   border-radius: 50%;
   pointer-events: none;
   &::after, &::before {
      @include vcenter;
      background: $white;
      border-radius: 50%;
      content: ''; }
   &::before {
      pointer-events: auto;
      z-index: 1;
      width: 45px;
      height: 45px;
      box-shadow: 0 15px 40px rgba(26, 3, 4, 0.2), 0 0 48px 3px rgba(240, 67, 79, 0.2);
      background-color: $color1; }
   &::after {
      width: 100px;
      height: 108px; }

   svg {
      z-index: 3;
      font-size: 16px;
      fill: $white; } }
// end .scroll-next-btn //
